
/* Histogram.jsx */
import React from 'react';
import {
  BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Cell
} from 'recharts';

const colorPalette = [
  '#c1dcc7', '#4daf4a', '#377eb8', '#984ea3', '#ff7f00',
  '#ffff33', '#a65628', '#f781bf', '#999999', '#66c2a5'
];
function isDiscrete(values) {
  return values.every(v => Number.isInteger(v));
}


function Histogram({ values, panelId, onHighlight, highlightedSite, highlightOrigin, linkedTo, height}) {
  const data = values.map((value, index) => ({ site: index, value }));
  const min = Math.min(...values), max = Math.max(...values);
  const unique = Array.from(new Set(values));
  const discrete = isDiscrete(values) && unique.length <= colorPalette.length;
  const cmap = discrete ? Object.fromEntries(unique.map((v,i)=>[v, colorPalette[i]])) : null;

  const getColor = v => {
    if (discrete) return cmap[v];
    const t = Math.sqrt((v-min)/(max-min||1)); return `rgb(255,${Math.round(255*(1-t))},${Math.round(255*(1-t))})`;
  };

  return (
    <>
      {discrete && (
        <div className="flex flex-wrap items-center mb-2 space-x-4">
          {unique.map((v, i) => (
            <div key={v} className="flex items-center space-x-1">
              <span
                className="inline-block w-4 h-4 rounded"
                style={{ background: cmap[v], border: '1px solid #ccc' }}
              />
              <span className="text-xs">{v}</span>
            </div>
          ))}
        </div>
      )}
      <ResponsiveContainer width="100%" height={height}>
        <BarChart data={data} margin={{ bottom: 30 }}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="site" label={{ value: 'Site', position: 'insideBottom', offset: 10 }} height={50} interval={Math.max(0, Math.floor(Math.sqrt(values.length)) - 1)}/>
          <YAxis label={{ value: 'Value', angle: -90, position: 'insideLeft' }} />
          <Tooltip />
          <Bar dataKey="value">
            {data.map((entry, index) => {
              const isLinkedHighlight = highlightedSite === index && linkedTo === highlightOrigin;
              return (
                <Cell
                  key={`cell-${index}`}
                  fill={getColor(entry.value)}
                  onMouseEnter={() => onHighlight(index, panelId)}
                  onMouseLeave={() => onHighlight(null, panelId)}
                  className={isLinkedHighlight ? 'histogram-highlight' : ''}
                />
              );
            })}
          </Bar>
        </BarChart>
      </ResponsiveContainer>
    </>
  );
}

export default Histogram;
